import route from "@/common/public.js"
import Prompt from '@/components/zz-prompt/index.vue'
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default {
  components: {
	Prompt,uniPopup
  },
  data() {
	return {
	  Signboard:'',
	  Signguid:'',
	  name: '', //用户名
	  telephone:'', //联系电话
	  sex:'', //性别
	  wechat:'', //微信号
	  qq:'', //qq号
	  address:'', //地址
	  sexFrame:false, /* 修改性别的弹出框 */
	  /* popupmodel: '', */
	  dis:'', //判断是公司账号还是user账号
	  array: ['女', '男'], //性别数组
	  /* unicommonHeight: false, */
	  /* windowHeight: '', */
	  disUser:'', //是否禁用输入框
	  height:''
	}
},
methods: {
	bindPickerChange: function(e) {
		 this.sex = e.target.value
	 },
	nameAndTelClick(val){
		if(val=='name'){
			if(this.dis=="USER"){
				this.disUser=true;
				uni.showToast({title:'请联系主账户，修改用户名',icon:'none'})
			}
			this.disUser=false;
		}else{
			uni.showToast({title: '请前往安全中心更改绑定手机号！',icon:"none"})
		}
	},
	
	/* 点击保存按钮触发的事件 */
	changePersonal(evt){
		// if(this.wechat==""){
		// 	uni.showToast({title:'请填写微信号',icon:'none'})
		// 	return false;
		// }
		// if(this.qq==""){
		// 	uni.showToast({title:'请填写QQ号',icon:'none'})
		// 	return false;
		// }
		// if(this.address==""){
		// 	uni.showToast({title:'请填写公司地址信息',icon:'none'})
		// 	return false;
		// }
		if(this.name==""){
			uni.showToast({title:'请填写用户名',icon:'none'})
			return false;
		}
		
		if(this.wechat!=""){
			if(!RegExp(/^[-_a-zA-Z0-9]{5,19}$/).test(this.wechat)){
				console.log("进来查看微信")
				uni.showToast({title:'微信号格式不正确',icon:'none'})
				return false;
			}
		}
		
		if(this.qq!=''){
			if(!RegExp(/^[0-9]{5,12}$/).test(this.qq)){
				uni.showToast({title:'QQ格式不正确',icon:'none'})
				return false;
			}
		}
		uni.request({
			url:route.variable+'/mobile/personal/altering_info',
			method:'POST',
			data:{
				Ident_Signboard: this.Signboard,
				Ident_Signguid: this.Signguid,
				loginname:this.name, //用户名
				qq:this.qq, //QQ号
				wechat:this.wechat, //微信号
				address:this.address, //地址
				sex:this.sex //性别
			},
			success: (res) => {
				if (route.publicIf(res.data.status) == false){
					return false;
				}
				if(res.data.status==0){
					uni.showToast({title:"保存成功",icon:"none"})
				}else{
					uni.showToast({title:'保存失败',icon:'none'})
				}
			},
			fail:(res)=>{
				uni.showToast({title: '信息保存失败'+'错误码201',icon:"none"});
			}
		})
	},
	isNull(userInfo){ //判断返回的数据是否为空，如果为空则将null换成字符串的空
		this.name=userInfo.name;
		this.telephone=userInfo.telephone;
		this.sex=userInfo.sex;
		this.wechat=userInfo.wechat;
		this.qq=userInfo.qq;
		this.address=userInfo.address;
		if(userInfo.wechat==null){
			this.wechat="";
		}
		if(userInfo.qq==null){
			this.qq="";
		}
		if(userInfo.address==null){
			this.address="";
		}
	}
  },
onLoad:function(){
	try {
	    this.height = this.winHeight
	} catch (e) {
	    // error
	}
	var jsonList=uni.getStorageSync("jsonList");
	var data = JSON.parse(jsonList); //JSON字符串转对象
	this.Signboard = data.Ident_Signboard;
	this.Signguid = data.Ident_Signguid;
	this.dis=data.dis;
	uni.request({
		url:route.variable+'/mobile/personal/get_user_info',
		method: 'GET',
		data:{
			Ident_Signguid: this.Signguid,
			Ident_Signboard:this.Signboard,
		},
		success: (res) => {
			if (route.publicIf(res.data.status) == false){
				return false;
			}
			if(res.data.status == 0){
				this.isNull(res.data.userInfo);
			}
		},
		fail:(res)=>{
			uni.showToast({title: '加载失败'+'错误码201',icon:"none"});
		}
	})
  }
}
